import sha1 from 'js-sha1'

export const getTimeStamp = () => {
  return Math.floor(new Date().getTime() / 1000)
}

export function generateSignature(nonceStr, timestamp, url) {
  const jsapi_ticket = 'sM4AOVdWfPE4DxkXGEs8VEWEL9bXA8Dq1qZa49kcK5RuFFrMtBFInFygq4meKVD9jo9UHkW1Tx0T6lbWPqMndw';
  console.log(url,'url')
  console.log(encodeURIComponent(url),'encodeURIComponent(url)')
  // const str = `jsapi_ticket=${'WP9kFb5NQXirJealNMgn2w=='}&noncestr=${nonceStr}&timestamp=${timestamp}&url=${encodeURIComponent(url)}`;
  const str = `jsapi_ticket=${jsapi_ticket}&noncestr=${nonceStr}&timestamp=${timestamp}&url=${url}`;
  console.log(str, 'str')
  return sha1(str);
  // return str;
}

export function generateNonceStr() {
  return Math.random().toString(36).substr(2, 15);
}