const baseUrl = `/api`

export const fetchGet = (url) =>
  new Promise(async (resolve) => {
    const dataObj = await fetch(`${baseUrl}${url}`, {
      headers: {
        'Content-Type': 'application/json',
        // 'Content-Type': 'image/svg+xml',
      },
      credentials: 'include',
    })
      .then((res) => resolve(res.text()))
  })
