<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
    <HelloWorld msg="Welcome to Your Vue.js App" />
    <button @click="btn">按钮11--</button>
    --
    <button @click="btn(1)">按钮11--</button>
    --------
    <button @click="btn2">按钮22</button>
  </div>
</template>

<script>
import { getTimeStamp, generateSignature, generateNonceStr } from './utils/wxShare'
import HelloWorld from './components/HelloWorld.vue'
export default {
  name: 'App',
  components: {
    HelloWorld
  },
  methods: {
    btn(val) {
      const _this = this
      const timeStamp = getTimeStamp()
      const nonceStr = generateNonceStr()
      const url = window.location.href.split('#')[0]
      console.log(url,'url')
      const signature = generateSignature(nonceStr, timeStamp, val === 1? 'https://www.txp-life.com':url)
      console.log(timeStamp, nonceStr, signature, 'signature')
      window.wx.config({
        beta: true,// 必须这么写，否则wx.invoke调用形式的jsapi会有问题
        debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        // appId: 'wwea7f0b932e20a266',
        appId: 'ww4c312544d1fb8b2d',
        timestamp: timeStamp,
        nonceStr,
        signature,
        jsApiList: [
          'getCurExternalContact',
          'getContext',
          'getCurCorpGroupContact'
        ]
      });
    },
    btn2() {
      wx.ready(() => {
        console.log(wx.invoke, 'invoke')
        wx.invoke('getContext', {
        }, function (res) {
          console.log(res, 'res-invoke')
          if (res.err_msg == 'getContext:ok') {
            entry = res.entry // 返回进入H5页面的入口类型，目前有normal、contact_profile、single_chat_tools、group_chat_tools、chat_attachment
            shareTicket = res.shareTicket // 可用于调用getShareInfo接口
          } else {
            // 错误处理
          }
        })
      })
      wx.error(function (res) {
        console.log(res, 'res-err')
        // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
      })
    }
  }
}
</script>

